.Modal_Modal__2CRFd {
    position: fixed;
    z-index: 500;
    background-color: transparent;
    width: 80%;
    padding: 16px;
    left: 15%;
    top: 10%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    text-align: left;
    overflow-y: auto;
}

@media (min-width: 600px) {
    .Modal_Modal__2CRFd {
        width: 500px;
        left: calc(50% - 250px);
    }
}
